<script>
import 'veui/locale/zh-Hans/common.js'
import 'veui/locale/en-US/common.js'
import 'veui/locale/zh-Hans/Transfer.js'
import 'veui/locale/en-US/Transfer.js'
import 'veui-theme-dls/components/Transfer.js'

import CandidatePanel from './_CandidatePanel'
import SelectedPanel from './_SelectedPanel'
import { includes, isString } from 'lodash'
import prefix from '../../mixins/prefix'
import ui from '../../mixins/ui'
import input from '../../mixins/input'
import useControllable from '../../mixins/controllable'
import { focusIn } from '../../utils/dom'
import { isEqualSet } from '../../utils/lang'
import { checkAll, clearItem, clearAll, getCheckedSubTree } from '../Tree/_tree'

function defaultFilter (type, keyword, item, datasource) {
  return includes(item.label, keyword)
}

export default {
  name: 'veui-transfer',
  mixins: [
    prefix,
    ui,
    input,
    useControllable({
      prop: 'selected',
      event: 'select',
      get (val) {
        return val || []
      }
    })
  ],
  model: {
    prop: 'selected',
    event: 'select'
  },
  props: {
    datasource: {
      type: Array,
      default () {
        return []
      }
    },
    searchable: {
      type: Boolean,
      default: true
    },
    filter: {
      type: Function,
      default: defaultFilter
    },
    selected: {
      type: Array,
      default () {
        return []
      }
    },
    candidatePlaceholder: String,
    selectedPlaceholder: String,
    selectedShowMode: {
      type: String,
      default: 'tree',
      validator (value) {
        return includes(['tree', 'flat'], value)
      }
    },
    keys: {
      type: [String, Function],
      default: 'value'
    }
  },
  computed: {
    isSelectable () {
      return !this.realDisabled && !this.realReadonly
    },
    realKeys () {
      if (isString(this.keys)) {
        return source => source[this.keys]
      }

      return this.keys
    },
    selectedItems () {
      return getCheckedSubTree(this.datasource, this.realSelected, 'all')
    }
  },
  methods: {
    focus () {
      focusIn(this.$el)
    }
  },
  render (h) {
    function generateHead (type) {
      let headSlotName = `${type}-head`
      let head = this.$slots[headSlotName]
        ? h('template', { slot: 'head' }, this.$slots[headSlotName])
        : null

      let titleSlotName = `${type}-title`
      let title =
        !head && this.$slots[titleSlotName]
          ? h('template', { slot: 'title' }, this.$slots[titleSlotName])
          : null

      return [head, title]
    }

    function generateItem (type) {
      let itemSlotName = `${type}-item`
      let item = this.$scopedSlots[itemSlotName]
        ? props => this.$scopedSlots[itemSlotName](props)
        : null

      let itemLabelSlotName = `${type}-item-label`
      let itemLabel =
        !item && this.$scopedSlots[itemLabelSlotName]
          ? props => this.$scopedSlots[itemLabelSlotName](props)
          : props => null

      return {
        item,
        'item-label': itemLabel
      }
    }

    function generateNoData (type) {
      let slotName = `${type}-no-data`
      return this.$slots[slotName]
        ? h('template', { slot: 'no-data' }, this.$slots[slotName])
        : null
    }

    return h(
      'div',
      {
        class: {
          [this.$c('input-invalid')]: this.realInvalid,
          [this.$c('transfer')]: true,
          [this.$c('transfer-disabled')]: !this.isSelectable
        },
        attrs: {
          ui: this.ui
        }
      },
      [
        h(
          CandidatePanel,
          {
            props: {
              datasource: this.datasource,
              searchable: this.searchable,
              filter: this.filter,
              placeholder: this.candidatePlaceholder,
              isSelectable: this.isSelectable,
              selected: this.realSelected,
              uiParts: this.uiParts,
              ui: this.realUi
            },
            on: {
              select: value => {
                this.commit('selected', value)
              },
              selectall: filtered => {
                if (filtered && filtered.length) {
                  let selected = checkAll(this.realSelected, filtered, 'all')
                  if (!isEqualSet(selected, this.realSelected)) {
                    this.commit('selected', selected)
                  }
                }
              }
            },
            scopedSlots: generateItem.call(this, 'candidate'),
            ref: 'candidatePanel'
          },
          [
            ...generateHead.call(this, 'candidate'),
            generateNoData.call(this, 'candidate')
          ]
        ),
        h(
          SelectedPanel,
          {
            props: {
              datasource: this.selectedItems,
              showMode: this.selectedShowMode,
              placeholder: this.selectedPlaceholder,
              isSelectable: this.isSelectable,
              icons: this.icons,
              uiParts: this.uiParts,
              ui: this.realUi
            },
            on: {
              remove: (item, parents) => {
                let selected = clearItem(
                  this.realSelected,
                  item,
                  parents,
                  this.datasource,
                  'all'
                )
                if (!isEqualSet(selected, this.realSelected)) {
                  this.commit('selected', selected)
                }
              },
              removeall: () => {
                if (this.datasource && this.datasource.length) {
                  let selected = clearAll(
                    this.realSelected,
                    this.datasource,
                    'all'
                  )
                  if (!isEqualSet(selected, this.realSelected)) {
                    this.commit('selected', selected)
                  }
                }
              }
            },
            scopedSlots: generateItem.call(this, 'selected')
          },
          [
            ...generateHead.call(this, 'selected'),
            generateNoData.call(this, 'selected')
          ]
        )
      ]
    )
  }
}
</script>
<style lang="less" src="veui-theme-dls/components/transfer.less"></style>
