import { find as _find, isPlainObject } from 'lodash'

export function walk (array, callback, alias = 'children', ctx = {}) {
  return _walk(array, callback, { ...ctx, depth: 0, parents: [] }, alias)
}

function _walk (array, callback, context, alias = 'children') {
  if (!array || !callback) {
    return
  }

  let enter
  let exit
  if (typeof callback === 'function') {
    enter = callback
  } else {
    enter = callback.enter
    exit = callback.exit
  }

  if (!enter && !exit) {
    return
  }

  if (!Array.isArray(array)) {
    array = getChildrenByAlias(array, alias)
    if (!array) {
      return
    }
  }

  let { depth, parents } = context
  let childrenResult = []
  array.forEach((item, index) => {
    let ctx = { ...context, index }
    let skipChildren = false

    if (typeof enter === 'function') {
      let ret = enter(item, ctx)
      if (ret === false) {
        skipChildren = true
      } else if (isPlainObject(ret)) {
        // 支持父给子传递上下文
        ctx = ret
      }
    }

    let children = getChildrenByAlias(item, alias)
    if (children && !skipChildren) {
      ctx.childrenResult = _walk(
        children,
        callback,
        { ...ctx, parents: [...parents, item], depth: depth + 1 },
        alias
      )
    }

    if (typeof exit === 'function') {
      childrenResult.push(exit(item, ctx))
    }
  })
  return childrenResult
}

export function find (array, predicate, alias = 'children') {
  if (!array || typeof predicate !== 'function') {
    return null
  }

  let result = null
  array.some(item => {
    if (predicate(item)) {
      result = item
      return true
    }
    let children = getChildrenByAlias(item, alias)
    if (!children) {
      return false
    }
    let inner = find(children, predicate, alias)
    if (inner !== null) {
      result = inner
      return true
    }
  })
  return result
}

function getChildrenByAlias (obj, alias) {
  let keys = typeof alias === 'string' ? [alias] : alias

  let key = _find(keys, key => {
    return Array.isArray(obj[key])
  })

  return key ? obj[key] : null
}
