<template>
<abstract-tree
  :class="{
    [$c('tree')]: true,
    [$c('disabled')]: realDisabled || realReadonly
  }"
  :group-class="$c('tree-item-group')"
  :ui="realUi"
  :items="normalizedItems"
  :expanded="realExpanded"
  @focusin.native="focused = true"
  @focusout.native="focused = false"
>
  <template #item="{ item, index, depth, expanded: itemExpanded, parents }">
    <div
      ref="item"
      :class="{
        [$c('tree-item')]: true,
        [$c('tree-item-expanded')]: itemExpanded,
        [$c('tree-item-selected')]: selectable && isSelected(item),
        [$c('tree-item-clickable')]: isClickable(item),
        [$c('tree-item-hidden')]: item.hidden,
        [$c('disabled')]: isDisabled(item, parents)
      }"
      :tabindex="depth === 1 ? item.tabIndex : item.disabled ? null : '-1'"
      @click="handleItemClick(item, parents, index, depth)"
      @keydown="handleKeydown($event, item, index, depth)"
    >
      <slot
        name="item"
        v-bind="item"
        :item="item"
        :index="index"
        :depth="depth"
        :expanded="itemExpanded"
        :parents="parents"
      >
        <button
          v-if="item.children && item.children.length"
          type="button"
          :class="$c('tree-item-expand-switcher')"
          tabindex="-1"
          @click.stop="toggleExpanded(item, index, depth)"
        >
          <veui-icon :name="icons.collapse"/>
        </button>
        <slot
          name="item-before"
          v-bind="item"
          :item="item"
          :index="index"
          :depth="depth"
          :expanded="itemExpanded"
          :parents="parents"
        >
          <veui-checkbox
            v-if="checkable"
            :class="$c('tree-item-check')"
            :checked="item.checked"
            :indeterminate="item.partialChecked"
            :disabled="realReadonly || isDisabled(item, parents)"
            :ui="realUi"
            tabindex="-1"
            @click.native.stop
            @change="handleItemCheck(item, parents)"
          />
        </slot>
        <div :class="$c('tree-item-label')">
          <slot
            name="item-label"
            v-bind="item"
            :item="item"
            :index="index"
            :depth="depth"
            :expanded="itemExpanded"
            :parents="parents"
          >
            {{ item.label }}
          </slot>
        </div>
        <slot
          name="item-after"
          v-bind="item"
          :item="item"
          :index="index"
          :depth="depth"
          :expanded="itemExpanded"
          :parents="parents"
        />
      </slot>
    </div>
  </template>
</abstract-tree>
</template>

<script>
import 'veui/locale/zh-Hans/common.js'
import 'veui/locale/en-US/common.js'
import 'veui-theme-dls/components/Tree.js'

import AbstractTree, { checkIncluded } from './_AbstractTree'
import Checkbox from '../Checkbox'
import Icon from '../Icon'
import prefix from '../../mixins/prefix'
import ui from '../../mixins/ui'
import input from '../../mixins/input'
import useControllable from '../../mixins/controllable'
import { includes, map, filter, uniq, cloneDeep, difference } from 'lodash'
import { focusIn, closest } from '../../utils/dom'
import warn from '../../utils/warn'
import { markChecked, toggleItem } from './_tree'

export default {
  name: 'veui-tree',
  components: {
    'veui-checkbox': Checkbox,
    'veui-icon': Icon,
    AbstractTree
  },
  mixins: [
    prefix,
    ui,
    input,
    useControllable([
      { prop: 'checked', event: 'check' },
      'selected',
      'expanded'
    ])
  ],
  model: {
    prop: 'checked',
    event: 'check'
  },
  props: {
    checkable: Boolean,
    checked: {
      type: Array,
      default () {
        return []
      }
    },
    selectable: Boolean,
    /* eslint-disable vue/require-prop-types */
    selected: {},
    /* eslint-ensable vue/require-prop-types */
    datasource: AbstractTree.props.items,
    expanded: AbstractTree.props.expanded,
    checkStrategy: {
      type: String,
      validator (val) {
        // all = leaf + 子全选中则父也在 checked 数组中
        // leaf = checked 只要叶子节点
        // branch = 子全选中则只要父节点（尽可能往上合并）
        // allWithIndeterminate = all + 所有部分选中的父
        return includes(['all', 'leaf', 'branch', 'allWithIndeterminate'], val)
      },
      default: 'all'
    }
  },
  data () {
    return {
      focused: false,
      focusVisible: map(this.datasource, _ => false)
    }
  },
  computed: {
    // 不在 normalize 中复制的目的：仅仅 checked 发生变化无需重新复制
    realItems () {
      let tree = this.checkable ? cloneDeep(this.datasource) : this.datasource
      tree = tree || []
      // 没复制过，浅复制下，后面要设置tabIndex
      if (!this.checkable) {
        tree = tree.map(i => ({ ...i }))
      }
      let firstTabable
      // 在 topLevel 找下第一个可以用 tab 获取焦点的
      tree.forEach(item => {
        item.tabIndex = item.disabled ? null : firstTabable ? '-1' : '0'
        firstTabable = item.tabIndex === '0' ? item : firstTabable
      })
      return tree
    },
    normalizedItems () {
      return this.checkable
        ? markChecked(this.realItems, this.realChecked, this.checkStrategy)
        : this.realItems
    },
    // 用来临时修改叶子节点的 checked 信息，然后递归下得到完整的 checked 数据
    includeIndeterminate () {
      return this.checkStrategy === 'allWithIndeterminate'
    },
    isBranchStrategy () {
      return this.checkStrategy === 'branch'
    }
  },
  methods: {
    isSelected (item) {
      let selected = this.realSelected != null ? [this.realSelected] : null
      return checkIncluded(selected, item)
    },
    isClickable (item) {
      return (
        this.selectable ||
        this.checkable ||
        (item.children && item.children.length)
      )
    },
    toggleExpanded (item, index, depth, expand) {
      let included = checkIncluded(this.realExpanded, item)
      let { name, value, children } = item
      if (expand === included || !children || children.length === 0) {
        return
      }
      let nameOrValue = name != null ? name : value
      if (nameOrValue == null) {
        warn('item value or name is required on expanding!', this)
        return
      }

      let expanded = included
        ? filter(this.realExpanded, expand => expand !== nameOrValue)
        : uniq([...this.realExpanded, nameOrValue])

      this.commit('expanded', expanded)
      this.$emit(included ? 'collapse' : 'expand', item, index, depth)
    },
    handleKeydown (e, item, index, depth) {
      let passive = false
      let expanded = checkIncluded(this.realExpanded, item)
      switch (e.key) {
        case 'Enter':
          this.toggleExpanded(item, index, depth)
          break
        case 'Left':
        case 'ArrowLeft':
          if (expanded) {
            this.toggleExpanded(item, index, depth, false)
          } else {
            this.focusLevel(e.target)
          }
          break
        case 'Right':
        case 'ArrowRight':
          if (expanded) {
            this.focusLevel(e.target, false)
          } else {
            this.toggleExpanded(item, index, depth, true)
            this.focusLevel(e.target, false)
          }
          break
        case 'Up':
        case 'ArrowUp':
          this.navigate(e.target, false)
          break
        case 'Down':
        case 'ArrowDown':
          this.navigate(e.target, true)
          break
        case 'Home':
          this.navigate(e.target, false, true)
          break
        case 'End':
          this.navigate(e.target, true, true)
          break
        default:
          passive = true
          break
      }
      if (!passive) {
        e.stopPropagation()
        e.preventDefault()
      }
    },
    getFocusSelector () {
      return `.${this.$c('tree-item')}:not(.${this.$c('disabled')})`
    },
    focusLevel (current, up = true) {
      let el
      if (up) {
        el = closest(closest(document.activeElement, 'li').parentNode, 'li')
      } else {
        el = document.activeElement.nextElementSibling
      }
      if (!el) {
        return
      }

      let itemEl = el.querySelector(this.getFocusSelector())
      if (itemEl) {
        this.$nextTick(() => {
          current.tabIndex = -1
          itemEl.tabIndex = 0
          itemEl.focus()
        })
      }
    },
    focus () {
      focusIn(this.$el)
    },
    navigate (current, forward = false, hitBoundary = false) {
      let context = this.$el
      current.tabIndex = -1
      let items = [...context.querySelectorAll(this.getFocusSelector())]
      let index = items.indexOf(current)

      let next
      if (hitBoundary) {
        next = items[forward ? items.length - 1 : 0]
        next.tabIndex = 0
        next.focus()
        return
      }

      let targetIndex =
        index === -1
          ? 0
          : ((forward ? index + 1 : index - 1) + items.length) % items.length
      next = items[targetIndex]
      next.tabIndex = 0
      next.focus()
    },
    handleItemClick (item, parents, ...extraArgs) {
      if (item.disabled) {
        return
      }

      let { name, value, children } = item
      let nameOrValue = name != null ? name : value
      if (this.selectable) {
        if (nameOrValue == null) {
          warn('item value or name is required on selecting!', this)
        } else {
          this.commit('selected', this.isSelected(item) ? null : nameOrValue)
        }
      } else if (this.checkable && !this.realDisabled && !this.realReadonly) {
        this.handleItemCheck(item, parents)
      } else if (children && children.length) {
        this.toggleExpanded(item, ...extraArgs)
      }
      this.$emit('click', item, parents, ...extraArgs)
    },
    isDisabled (item, parents) {
      return (
        !!this.realDisabled ||
        !!item.disabled ||
        parents.some(i => !!i.disabled)
      )
    },
    handleItemCheck (item, parents) {
      let newChecked = toggleItem(
        this.realChecked,
        item,
        parents,
        this.normalizedItems,
        this.checkStrategy
      )
      if (
        difference(newChecked, this.realChecked).length ||
        difference(this.realChecked, newChecked).length
      ) {
        this.commit('checked', newChecked)
      }
    }
  }
}
</script>
<style lang="less" src="veui-theme-dls/components/tree.less"></style>
